@import url("https://fonts.googleapis.com/css2?family=Coustard:wght@900&family=Inter&display=swap");

*,
*::before,
*::after {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Inter", sans-serif;
  font-size: 1.3125rem;
  line-height: 1.6;
}

:root {
  --bg-header: #fff;
  --bg-color: #000;
  --bg-color-accent: #78b8a0;
  --bg-color-three: #7890b8;
  --color-black: #000;
  --color-white: #fff;
}
/* :root {
  --bg-header: rgba(245, 245, 245, 0.815);
  --bg-color: #b88078;
  --bg-color-accent: #78b8a0;
  --bg-color-three: #7890b8;
} */

img {
  max-width: 100%;
}

h1,
h2 {
  font-family: "Coustard", serif;
  margin-top: 0;
  font-weight: 900;
  line-height: 1.1;
}

section {
  padding: 4em 0;
}

.container {
  width: 90%;
  max-width: 1128px;
  margin: 0 auto;

  position: relative;
}

.grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2em 1em;

  margin: 2em 0;
}

header {
  background-color: var(--bg-color-three);
  padding: 0.75rem 0;
  text-align: center;
}

a {
  text-decoration: none;
  color: #000;
}

.btn {
  cursor: pointer;
  position: relative;
  z-index: 1;
}

.btn::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: -1;
  -webkit-transform: scaleX(0);
      -ms-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: right;
      -ms-transform-origin: right;
          transform-origin: right;
  -webkit-transition: -webkit-transform 200ms ease-in;
  transition: -webkit-transform 200ms ease-in;
  -o-transition: transform 200ms ease-in;
  transition: transform 200ms ease-in;
  transition: transform 200ms ease-in, -webkit-transform 200ms ease-in;
}

.btn:hover::after {
  -webkit-transform: scaleX(1);
      -ms-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: left;
      -ms-transform-origin: left;
          transform-origin: left;
}

.form__btn {
  display: block;
  margin-top: 1em;
  border-radius: 16px;
  padding: 0.3em 2em;
}

.form__btn::after,
.form__btn:hover::after {
  border-radius: 16px;
}

/* ****************************** */ /* *********** NAV ************* */ /* ****************************** */

.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  -ms-flex-align: center;
      align-items: center;
}

.nav__list--primary {
  margin-bottom: 2em;
}

.nav__link {
  /* color: #fff; */
  font-size: 1rem;
  text-decoration: none;
  text-transform: uppercase;
  position: relative;
}

.nav__link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 3px;
  /* background-color: var(--color-black); */
  background-color: var(--bg-color-accent);
  -webkit-transform: scaleX(0);
      -ms-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: right;
      -ms-transform-origin: right;
          transform-origin: right;
  -webkit-transition: -webkit-transform 300ms ease-in;
  transition: -webkit-transform 300ms ease-in;
  -o-transition: transform 300ms ease-in;
  transition: transform 300ms ease-in;
  transition: transform 300ms ease-in, -webkit-transform 300ms ease-in;
}

.nav__link:hover::after {
  -webkit-transform: scaleX(1);
      -ms-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: left;
      -ms-transform-origin: left;
          transform-origin: left;
}

.nav__link--relative {
  position: relative;
  font-size: 1.25rem;
}

.logo {
  -ms-flex-item-align: center;
      align-self: center;
}

.nav-toggle {
  color: var(--color-black);
  /* color: red; */
  cursor: pointer;
  width: 3rem;
  height: 2.5rem;

  position: absolute;
  left: 0;
}

.nav-toggle:focus,
.nav-toggle:hover {
  opacity: 0.85;
}

.nav {
  width: 100%;
  visibility: hidden;
  height: 0;
  position: absolute;
}

.nav--visible {
  visibility: visible;
  height: auto;
  position: relative;
}

.nav__item {
  margin-top: 0.5em;
}

.nav__span {
  color: #fff;
  font-size: 11px;
  position: absolute;
  left: 14px;
  bottom: 14px;
}

/* ****************************** */ /* *********** HOME ************* */ /* ****************************** */
.intro {
  /* background-image: "../images/pizza.jpg"; */
  min-height: 660px;
  padding: 200px 0;
  text-align: center;
  background-color: var(--color-white);
  color: var(--color-black);
  /* background-color: #f3eed9; */
  /* color: #824936; */
}

.intro__title {
  font-size: 3rem;
}

.intro__title--colored {
  color: var(--color-black);
}

.section-three {
  background-color: var(--color-black);
  /* background-color: #824936; */
  color: #fff;
}

.iframe__wrapper {
  position: relative;
  overflow: hidden;
  padding-top: 56.25%;
}

.iframe__wrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/* ****************************** */ /* *********** SHOP ************* */ /* ****************************** */
.shop {
  margin-top: 1em;
}

.card {
  /* border: 2px solid #727272; */
  /* background: rgba(247, 247, 247, 0.658); */
  border-radius: 20px;
  text-align: center;
  margin-bottom: 1em;
}

.card:hover {
  -webkit-box-shadow: 0 0.5rem 0.5rem 0 rgba(0, 0, 0, 0.35);
          box-shadow: 0 0.5rem 0.5rem 0 rgba(0, 0, 0, 0.35);
}

.search-bar {
  /* border: 2px solid salmon; */
  /* border: 2px solid var(--color-black); */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: end;
}

.search-bar__input {
  /* flex-grow: 1; */
  font-size: 1rem;
  min-width: 25%;
  padding: 5px 10px;
  border: 2px solid black;
  border-radius: 15px;
}

.heart {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: start;
  padding: 0 5px;
  -ms-flex-align: center;
      align-items: center;
}

.heart p {
  font-size: 0.8rem;
  margin: 0;
}

.heart-icon {
  cursor: pointer;
  margin-right: 0.25em;
}

.heart-icon:hover {
  color: red;
}

.card-text h4,
.card-text p {
  margin-top: 0;
  margin-bottom: 10px;
}

.card-text p {
  font-size: 1rem;
}

/* ****************************** */ /* *********** PRODUCT ************* */ /* ****************************** */

.product__section {
  /* background: moccasin; */
  background: var(--color-white);
}

/* ****************************** */ /* *********** CART ************* */ /* ****************************** */
.cart {
  margin-top: 2em;
}

.empty-cart {
  font-size: 3rem;
  text-align: center;
  padding-top: 2em;
}

.cart--card {
  display: grid;
  grid-template-columns: 1fr 1fr;
  -ms-flex-align: center;
      align-items: center;
  justify-items: center;
  padding: 2em 0;
  border-bottom: 2px solid rgba(100, 100, 100, 0.5);
}
.card-section {
  font-size: 2rem;
  padding: 0.2em;
}

.check-out__btn {
  background-color: rgb(240 200 100);
}

.cart__footer {
  padding: 2em 0;
}

.cart__footer h4 {
  margin: 0;
}
/* ****************************** */ /* *********** MEDIA ************* */ /* ****************************** */
@media (min-width: 750px) {
  /* *********** body ************* */
  .container {
    width: 85%;
  }

  .row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }

  /* experiment */
  .product__section {
    padding: 2em 0;
  }

  .row--center {
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
  }

  .detail {
    max-height: 500px;
  }

  /* experiment */

  .col {
    width: 100%;
  }

  .col + .col {
    margin-left: 5em;
  }

  .grid {
    /* display: grid;
    gap: 2em 1em; */
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
  /* *********** nav ************* */

  .nav {
    /* border: 3px solid red; */
    visibility: visible;
    position: unset;
    display: -ms-flexbox;
    display: flex;
    height: auto;
    -ms-flex-positive: 1;
        flex-grow: 1;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
  }

  .nav__list {
    display: -ms-flexbox;
    display: flex;
    /* border: 3px solid blue; */
  }

  .nav__list--primary {
    margin: 0 auto;
  }

  .nav__item {
    margin: 0;
  }

  .nav__item + .nav__item {
    margin-left: 1em;
  }

  .nav-toggle {
    display: none;
  }

  /* .navbar {
    all: unset;
    display: flex;
    align-items: center;
    width: 40%;
    margin-right: 1em;
    justify-content: space-evenly;
  }

  .navbar.active {
    all: unset;
    display: flex;
    align-items: center;
    width: 40%;
    margin-right: 1em;
    justify-content: space-evenly;
  }


  li.nav-link,
  .cart-symbol {
    all: unset;
  } */

  /* *********** home ************* */
  .intro__title {
    font-size: 3.75rem;
  }
}



